'use client';
import React, { useState, useEffect } from 'react';
import { useApp } from '@/context/AppContext';
import Icon from '@/components/ui/Icons';

export default function Hero() {
  const { language } = useApp();
  const [currentRole, setCurrentRole] = useState(0);

  const roles = [
    "UI/UX Designer",
    "Web Developer", 
    "Brand Strategist",
    "Creative Director"
  ];

  const content = {
    title: {
      pt: 'Alexander Chen',
      en: 'Alexander Chen'
    },
    subtitle: {
      pt: 'Desenvolvedor Web Full-Stack',
      en: 'Full-Stack Web Developer'
    },
    description: {
      pt: 'Apaixonado por criar experiências digitais excepcionais que combinam design inovador com desenvolvimento funcional. Vamos dar vida à sua visão.',
      en: 'Passionate about creating exceptional digital experiences that blend innovative design with functional development. Let\'s bring your vision to life.'
    },
    cta1: {
      pt: 'Ver Meus Projetos',
      en: 'View My Work'
    },
    cta2: {
      pt: 'Entrar em Contato',
      en: 'Get In Touch'
    }
  };

  const socialLinks = [
    { icon: 'dribbble' as const, href: '#' },
    { icon: 'behance' as const, href: '#' },
    { icon: 'github' as const, href: '#' },
    { icon: 'linkedin' as const, href: '#' },
  ];

  // Efeito de typing
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentRole((prev) => (prev + 1) % roles.length);
    }, 3000);

    return () => clearInterval(interval);
  }, []);

  return (
    <section id="home" className="min-h-screen relative overflow-hidden flex items-center bg-background">
      {/* Background Elements */}
      <div className="absolute inset-0 z-1 overflow-hidden">
        <div className="absolute w-72 h-72 bg-gradient-to-br from-accent/20 to-accent/10 rounded-full top-10 right-10 animate-float"></div>
        <div className="absolute w-48 h-48 bg-gradient-to-br from-accent/15 to-accent/5 rounded-full bottom-20 left-5 animate-float" style={{ animationDelay: '2s' }}></div>
      </div>

      <div className="container relative z-10 mx-auto px-6">
        <div className="grid lg:grid-cols-2 gap-12 items-center">
          {/* Text Content */}
          <div className="text-center lg:text-left">
            <h1 className="text-5xl lg:text-7xl font-bold text-heading font-heading mb-4 leading-tight">
              Snap<span className="text-accent">Folio</span>
            </h1>
            <h2 className="text-2xl lg:text-3xl text-gray-300 mb-6 font-heading">
              {content.title[language]}
            </h2>
            
            <div className="text-xl text-gray-400 mb-4 h-8">
              {language === 'pt' ? 'Eu sou' : 'I\'m a'}{' '}
              <span className="text-accent font-semibold border-b-2 border-accent transition-all duration-500">
                {roles[currentRole]}
              </span>
            </div>

            <p className="text-lg text-gray-400 mb-8 leading-relaxed max-w-lg">
              {content.description[language]}
            </p>

            {/* CTA Buttons */}
            <div className="flex flex-col sm:flex-row gap-4 mb-8 justify-center lg:justify-start">
              <a 
                href="#portfolio" 
                className="px-8 py-4 bg-accent text-contrast rounded-full font-semibold hover:bg-accent/90 transition-all duration-300 hover:translate-y-[-2px] shadow-lg hover:shadow-accent/25 flex items-center justify-center gap-2"
              >
                <Icon name="images" size={20} />
                {content.cta1[language]}
              </a>
              <a 
                href="#contact" 
                className="px-8 py-4 border-2 border-accent text-accent rounded-full font-semibold hover:bg-accent hover:text-contrast transition-all duration-300 hover:translate-y-[-2px] flex items-center justify-center gap-2"
              >
                <Icon name="mail" size={20} />
                {content.cta2[language]}
              </a>
            </div>

            {/* Social Links */}
            <div className="flex justify-center lg:justify-start space-x-4">
              {socialLinks.map((social, index) => (
                <a
                  key={index}
                  href={social.href}
                  className="w-12 h-12 flex items-center justify-center bg-surface rounded-full text-gray-400 hover:bg-accent hover:text-white transition-all duration-300 hover:translate-y-[-3px] shadow-lg hover:shadow-accent/25"
                  title={social.icon}
                >
                  <Icon name={social.icon} size={20} />
                </a>
              ))}
            </div>
          </div>

          {/* Profile Image */}
          <div className="flex justify-center lg:justify-end">
            <div className="relative w-80 h-80 lg:w-96 lg:h-96">
              <div className="absolute top-4 left-4 w-full h-full bg-accent/20 rounded-3xl"></div>
              <div className="relative w-full h-full bg-surface rounded-3xl overflow-hidden border-4 border-accent/30 shadow-2xl">
                <img 
                  src="/assets/img/profile/profile-2.webp" 
                  alt={content.title[language]}
                  className="w-full h-full object-cover"
                  onError={(e) => {
                    // Fallback para imagem
                    e.currentTarget.src = 'data:image/svg+xml;base64,';
                  }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}