'use client';
import React from 'react';
import { useApp } from '@/context/AppContext';
import Icon from '@/components/ui/Icons';

export default function Sidebar() {
  const { theme, language, toggleTheme, toggleLanguage } = useApp();

  const menuItems = [
    { name: { pt: 'Início', en: 'Home' }, href: '#home', icon: 'home' as const },
    { name: { pt: 'Sobre', en: 'About' }, href: '#about', icon: 'user' as const },
    { name: { pt: 'Currículo', en: 'Resume' }, href: '#resume', icon: 'fileText' as const },
    { name: { pt: 'Portfólio', en: 'Portfolio' }, href: '#portfolio', icon: 'images' as const },
    { name: { pt: 'Serviços', en: 'Services' }, href: '#services', icon: 'layers' as const },
    { name: { pt: 'Contato', en: 'Contact' }, href: '#contact', icon: 'mail' as const },
  ];

  const socialLinks = [
  { name: 'Twitter', icon: 'twitter' as const, href: '#' },
  { name: 'LinkedIn', icon: 'linkedin' as const, href: '#' },
  { name: 'Instagram', icon: 'instagram' as const, href: '#' },
  { name: 'Dribbble', icon: 'dribbble' as const, href: '#' },
  { name: 'Github', icon: 'github' as const, href: '#' },
];

  return (
    <div className="fixed left-0 top-0 h-screen w-80 bg-gray-900 border-r border-gray-700 flex flex-col p-6 font-nav z-50">
      {/* Logo/Nome */}
      <div className="mb-12">
        <h1 className="text-2xl font-bold text-white font-heading">SnapFolio</h1>
        <p className="text-sm text-gray-400 mt-2">
          {language === 'pt' ? 'Desenvolvedor Web' : 'Web Developer'}
        </p>
      </div>

      {/* Menu Navigation */}
      <nav className="flex-1">
        <ul className="space-y-2">
          {menuItems.map((item) => (
            <li key={item.href}>
              <a
                href={item.href}
                className="flex items-center py-3 px-4 text-gray-300 hover:bg-gray-800 hover:text-white rounded-lg transition-all duration-300 group"
              >
                <Icon 
                  name={item.icon} 
                  className="mr-3 text-gray-400 group-hover:text-accent transition-colors" 
                />
                <span className="font-medium">{item.name[language]}</span>
              </a>
            </li>
          ))}
        </ul>
      </nav>

      {/* Social Links */}
      <div className="mb-6">
        <div className="flex justify-center space-x-3">
          {socialLinks.map((social) => (
            <a
              key={social.name}
              href={social.href}
              className="w-10 h-10 flex items-center justify-center bg-gray-800 text-gray-400 rounded-full hover:bg-accent hover:text-white transition-all duration-300 hover:scale-110"
              title={social.name}
            >
              <Icon name={social.icon} size={18} />
            </a>
          ))}
        </div>
      </div>

      {/* Controls */}
      <div className="space-y-3 pt-6 border-t border-gray-700">
        {/* Theme Toggle */}
        <button
          onClick={toggleTheme}
          className="flex items-center justify-between w-full p-3 bg-gray-800 rounded-lg hover:bg-gray-700 transition-all duration-300 group"
        >
          <span className="text-gray-300 font-medium">
            {language === 'pt' ? 'Tema' : 'Theme'}
          </span>
          <div className="flex items-center space-x-2">
            {theme === 'light' ? (
              <Icon name="sun" className="text-yellow-500" size={20} />
            ) : (
              <Icon name="moon" className="text-blue-400" size={20} />
            )}
            <span className="text-sm text-gray-400 font-medium">
              {theme === 'light' ? 'Light' : 'Dark'}
            </span>
          </div>
        </button>

        {/* Language Toggle */}
        <button
          onClick={toggleLanguage}
          className="flex items-center justify-between w-full p-3 bg-gray-800 rounded-lg hover:bg-gray-700 transition-all duration-300 group"
        >
          <span className="text-gray-300 font-medium">
            {language === 'pt' ? 'Idioma' : 'Language'}
          </span>
          <span className="text-sm text-gray-400 font-medium px-2 py-1 bg-gray-700 rounded">
            {language === 'pt' ? 'PT' : 'EN'}
          </span>
        </button>
      </div>
    </div>
  );
}