import { 
  Home, 
  User, 
  FileText, 
  Images, 
  Layers, 
  Mail,
  Moon,
  Sun,
  Github,
  Linkedin,
  Twitter,
  Instagram,
  Dribbble,
  Phone,
  MapPin,
  Download,
  MessageCircle,
  CheckCircle,
  Award,
  Briefcase,
  GraduationCap,
  Code,
  Palette,
  Megaphone,
  BarChart,
  Camera,
  Heart,
  Star,
  Zap,
  Cloud,
  Settings
} from 'lucide-react';

export const Icons = {
  home: Home,
  user: User,
  fileText: FileText,
  images: Images,
  layers: Layers,
  mail: Mail,
  moon: Moon,
  sun: Sun,
  github: Github,
  linkedin: Linkedin,
  twitter: Twitter,
  instagram: Instagram,
  dribbble: Dribbble,
  behance: Heart, // Substituindo Behance por Heart
  phone: Phone,
  mapPin: MapPin,
  download: Download,
  messageCircle: MessageCircle,
  checkCircle: CheckCircle,
  award: Award,
  briefcase: Briefcase,
  graduationCap: GraduationCap,
  code: Code,
  palette: Palette,
  megaphone: Megaphone,
  barChart: BarChart,
  camera: Camera,
  heart: Heart,
  star: Star,
  zap: Zap,
  cloud: Cloud,
  settings: Settings
};

export type IconName = keyof typeof Icons;

interface IconProps {
  name: IconName;
  size?: number;
  className?: string;
}

export default function Icon({ name, size = 24, className = "" }: IconProps) {
  const LucideIcon = Icons[name];
  
  if (!LucideIcon) {
    console.warn(`Icon "${name}" not found`);
    return null;
  }
  
  return <LucideIcon size={size} className={className} />;
}